<template>
  <div>
    <!-- 面包屑---显示当前页面的路径，快速返回之前的任意页面-->
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-s-data"></i> 训练数据
        </el-breadcrumb-item>
        <el-breadcrumb-item>机位数据</el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="container">
      <div class="handle-box">
        <!-- 训练数据选择器 -->
        <span class="selectData">机位号:</span>
        <el-input
          placeholder="请输入机位号"
          v-model="selectGateNum"
          clearable
          style="width: 150px"
        ></el-input>
        <!-- 查询数据 -->
        <span class="selectData2">机位状态:</span>
        <!-- <el-input
          placeholder="请输入航班号"
          v-model="selectAirportNum"
          clearable
          style="width: 150px"
        ></el-input> -->
        <el-select
          class="handle-select mr10"
          v-model="selectStateData"
          placeholder="请选择机位状态"
        >
          <el-option
            v-for="item in stateList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <!-- 查询按钮 -->
        <el-button
          type="primary"
          icon="el-icon-search"
          class="handle-del btu-left-right"
          @click="selectNum"
          >查询</el-button
        >
        <!-- <el-button
          type="primary"
          class="handle-del btu-left-right"
          @click="selectAirDialog = true"
          >高级查询</el-button
        > -->
        <el-button class="handle-del btu-left-right" @click="selectNumCancel"
          >重置</el-button
        >
      </div>
      <div class="handle-box">
        <!-- 添加测试按钮 -->
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          class="handle-del mr10"
          @click="addGateDatadialogOpen"
          >新建</el-button
        >
        <!-- 批量删除测试数据按钮 -->
        <!-- <el-button
          type="danger"
          icon="el-icon-delete"
          class="handle-del mr10"
          @click="delGateDataDialog = true"
          >批量删除</el-button
        > -->
        <!-- 直接批量删除数据 -->
        <el-button
          type="danger"
          icon="el-icon-delete"
          class="handle-del mr10"
          @click="delGateData"
          >批量删除</el-button
        >
        <!-- 机场平面图 -->
        <!-- <el-button
          v-if="this.rolen === 'admin'"
          type="primary"
          class="handle-del mr10"
          @click="showairport"
        > -->
        <el-button
          type="primary"
          class="handle-del mr10"
          @click="showairport"
        >
          <i class="el-icon-position"></i>
          机场平面图
        </el-button>
      </div>

      <!-- 表格数据 -->
      <!-- 由于对tableData进行了过滤，导致对话框出现清空了多选框的选择状态 -->
      <el-table
        :data="
          tableData.slice((currentPage - 1) * PageSize, currentPage * PageSize)
        "
        border
        class="table"
        ref="multipleTable"
        @selection-change="handleSelectionChange"
        header-cell-class-name="table-header"
        row-key="parkinggate"
        :default-sort = "{prop: 'endtime'}"
      >
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          prop="parkinggate"
          label="机位号"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="airportname"
          label="机场名称"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="state"
          label="机位状态"
          align="center"
        >
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.state === '0'">关闭</el-tag>
            <el-tag type="success" v-if="scope.row.state === '1'">空闲</el-tag>
            <el-tag type="info" v-if="scope.row.state === '2'">占用</el-tag>
            <el-tag type="warning" v-if="scope.row.state === '3'">维护</el-tag>
          </template>
        </el-table-column>
        <!-- <el-table-column label="结束占用时间" align="center">
          <template slot-scope="scope">{{
            scope.row.endtime | atimeFormat
          }}</template>
        </el-table-column> -->
        <el-table-column prop="endtime" label="结束占用时间" align="center" sortable>
          <template slot-scope="scope">{{
            scope.row.endtime
          }}</template>
        </el-table-column>
        <el-table-column prop="nation" label="国际/国内" align="center">
          <template slot-scope="scope">
            <!-- 1-国内 2-国际 3-混合 -->
            <span v-if="scope.row.nation === '1'">国内</span>
            <span v-if="scope.row.nation === '2'">国际</span>
            <span v-if="scope.row.nation === '3'">混合</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200" align="center">
          <template slot-scope="scope">
            <el-button
              v-if="rolen === 'admin'"
              type="text"
              icon="el-icon-edit"
              @click="getUpdateGateInfo(scope.row.parkinggate)"
            >更改</el-button>
            <el-button
              type="text"
              icon="el-icon-delete"
              class="red"
              @click="handleDelete(scope.row.parkinggate)"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="pagination">
        <el-pagination
          background
          layout="total, sizes, prev, pager, next, jumper"
          @current-change="handleCurrentChange"
          @size-change="handleSizeChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          :total="totalCount"
        ></el-pagination>
      </div>
    </div>
    <!-- 机场平面图 -->
    <el-dialog :visible.sync="showsvg" width="80%" class="dialog" title="机场平面图">
