        // 根据id找到index
        let index = this.tableData.findIndex(item => {
          if (item.parkinggate == idlist[i]) {
            return true;
          }
        });
        let data = {
          parkinggate: this.tableData[index].parkinggate
        };
        datalist[i]=JSON.parse(JSON.stringify(data));
      } 
      console.log('datalist  '+JSON.stringify(datalist))
      if (this.rolen !== "admin") {
        this.$message({
          message: "对不起，您无权删除该数据！",
          type: "danger",
          offset: 300,
          center: true,
          duration: 1000
        });
      } else {
        // let data = {
        //   parkinggate: this.tableData[index].parkinggate
        // };
        
        // console.log('hhhh'+data)
        deleteGateData(datalist)
          .then(() => {
            this.$message({
              message: "删除成功！",
              type: "success",
              offset: 300,
              center: true,
              duration: 1000
            });
          })
          .then(() => {
            // 重新渲染表格
            this.getData();
          });
      }
