    // 根据机位号筛选
    selectNum() {
      this.tableData = JSON.parse(JSON.stringify(this.subTableData));
      let table = [];
      // let statevalue="";
      // console.log('state   '+this.selectStateData)
      // if(this.selectStateData=='关闭'){
      //   statevalue=0;
      // }else if(this.selectStateData=='空闲'){
      //   statevalue=1;
      // }else if(this.selectStateData=='占用'){
      //   statevalue=2;
      // }else{
      //   statevalue=3;
      // }
      if(this.selectStateData && this.selectGateNum){
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].parkinggate==this.selectGateNum && this.tableData[i].state==this.selectStateData) {
            table.push(this.tableData[i]);
          }
        }
      }else if(this.selectGateNum){
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].parkinggate==this.selectGateNum) {
            table.push(this.tableData[i]);
          }
        }
      }else{
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].state==this.selectStateData) {
            table.push(this.tableData[i]);
          }
        }
      }
      // for (let i = 0; i < this.tableData.length; i++) {
      //   if (this.tableData[i].parkinggate==this.selectGateNum) {
      //     table.push(this.tableData[i]);
      //   }
      // }
      this.tableData = table;
      this.totalCount = this.tableData.length;
      this.currentPage = 1;
    },
    //重置筛选
    selectNumCancel() {
      this.tableData = JSON.parse(JSON.stringify(this.subTableData));
      this.totalCount = this.tableData.length;
      this.currentPage = 1;
      this.selectGateNum = "";
      this.selectStateData = "";
