    // 更改表格现有机位数据
    getUpdateGateInfo(id) {
      // console.log('hhh  '+id)
      // 根据id找到index
      let index = this.tableData.findIndex(item => {
        if (item.parkinggate == id) {
          return true;
        }
      });
      let table=JSON.parse(JSON.stringify(this.tableData));
      if (table[index].state == '0') {
        table[index].state = "关闭";
      }else if(table[index].state == '1'){
        table[index].state = "空闲";
      }else if(table[index].state == '2'){
        table[index].state = "占用";
      }else if(table[index].state == '3'){
        table[index].state = "维护";
      }else{

      }

      if (table[index].bridge == '0') {
        table[index].bridge = "无";
      }else if(table[index].bridge == '1'){
        table[index].bridge = "有";
      }else{

      }

      if (table[index].nation == '1') {
        table[index].nation = "国内";
      }else if(table[index].nation == '2'){
        table[index].nation = "国际";
      }else if(table[index].nation == '3'){
        table[index].nation = "混合";
      }else{

      }
      this.gateInfoForm = JSON.parse(JSON.stringify(table[index]));
      this.updateDialog=true;
    },
    // 修改表单
    updateData() {
      // 封装插入的数据
      let data = {
        state: this.gateInfoForm.state,
        endtime: this.gateInfoForm.endtime,
        bridge: this.gateInfoForm.bridge,
        nation: this.gateInfoForm.nation,
        parkinggate: this.gateInfoForm.parkinggate
      };
      console.log('hhhhh:'+JSON.stringify(data))
      if(data.state=="关闭"){data.state='0';}
      if(data.state=="空闲"){data.state='1';}
      if(data.state=="占用"){data.state='2';}
      if(data.state=="维护"){data.state='3';}

      if(data.bridge=="无"){data.bridge='0';}
      if(data.bridge=="有"){data.bridge='1';}

      if(data.nation=="国内"){data.nation='1';}
      if(data.nation=="国际"){data.nation='2';}
      if(data.nation=="混合"){data.nation='3';}
      updateGateData(data).then(() => {
        // 显示数据
        let data = {
          airportname: this.airportname,
        };
        selectGateData(data).then((res) => {
          // 保存机场数据
          let tmptable=JSON.parse(JSON.stringify(res.data));
          this.gatestatelist.length=0;
          for(let i=0;i<tmptable.length;i++){
            tmptable[i].airportname=this.airportname;
            this.gatestatelist[i]=tmptable[i].state;
          }
          this.subTableData=JSON.parse(JSON.stringify(tmptable));
          this.tableData = JSON.parse(JSON.stringify(tmptable));
          this.totalCount = this.tableData.length;
        }).then(() => {
            this.$message({
              message: "修改成功！",
              type: "success",
              offset: 300,
              center: true,
              duration: 2000,
            });
          })
          .then(() => {
            // 关闭对话框
            this.updateDialog = false;
            // // 重置表单
            // this.resetGateInfoForm();
          });
        })
       
    },
    getData(){
      // 显示数据
      let data = {
        airportname: this.airportname,
      };
      selectGateData(data).then((res) => {
        // 保存机场数据
        let tmptable=JSON.parse(JSON.stringify(res.data));
        for(let i=0;i<tmptable.length;i++){
          tmptable[i].airportname=this.airportname;
        }
        this.subTableData=JSON.parse(JSON.stringify(tmptable));
        this.tableData = JSON.parse(JSON.stringify(tmptable));
        this.totalCount = this.tableData.length;
      }).then(() => {
          // this.$message({
          //   message: "成功！",
          //   type: "success",
          //   offset: 300,
          //   center: true,
          //   duration: 2000,
          // });
        })

