    // 删除一条数据
    handleDelete(id) {
      // 二次确认删除
      this.$confirm("确定要删除吗？", "提示", {
        type: "warning"
      })
        .then(() => {
          // 根据id找到index
          let index = this.tableData.findIndex(item => {
            if (item.parkinggate == id) {
              return true;
            }
          });
          // console.log('hhhh'+index)
          // 判断是否能删除
          // if (window.sessionStorage.namen !== this.tableData[index].inputName) {
          if (this.rolen !== "admin") {
            this.$message({
              message: "对不起，您无权删除该数据！",
              type: "danger",
              offset: 300,
              center: true,
              duration: 1000
            });
          } else {
            let data = {
              parkinggate: this.tableData[index].parkinggate
            };
            // console.log('hhhh'+data)
            deleteGateData(data)
              .then(() => {
                this.$message({
                  message: "删除成功！",
                  type: "success",
                  offset: 300,
                  center: true,
                  duration: 1000
                });
              })
              .then(() => {
                // 重新渲染表格
                this.getData();
              });
          }
        })
        .catch(() => {});
    },
    
    // 添加表单数据确认
    addGateData() {
      // if(typeof(this.addGateNum) !='number'){
      //   this.$message({
      //     message: "请输入数字类型机位号",
      //     type: "warning",
      //     offset: 300,
      //   });
      //   }
      //判断各字段是否为空
      if (this.addGateNum === "" || !(/^\d+$/).test(this.addGateNum)) {
        this.$message({
          message: "请输入数字类型机位号",
          type: "warning",
          offset: 300,
        });
      }else if (this.addBridge === ""){
        this.$message({
          message: "请选择是否有廊桥",
          type: "warning",
          offset: 300,
        });
      }else if (this.addNation === ""){
        this.$message({
          message: "请选择机位属性",
          type: "warning",
          offset: 300,
        });
      }else {
        // 判断机位号是否重复
        let checkgatelist = [];
        if (this.tableData.length != 0) {
          for (let i = 0; i < this.tableData.length; i++) {
            checkgatelist.push(this.tableData[i].parkinggate);
          }
        }
        if (checkgatelist.indexOf(this.addGateNum) != -1) {
          this.$message({
            message: "机位重复！请更换！",
            type: "error",
            offset: 300,
            center: true,
          });
        } else {
          // 保存数据
          let newGateData = {
            airportname: this.airportname,
            parkinggate: this.addGateNum,
            state: (this.addState),
            endtime: this.addEndtime,
            bridge: (this.addBridge),
            nation: (this.addNation),
            updateTime: getNewtime()
          };
          // console.log('ooooo:'+JSON.stringify(newGateData))
          console.log('ooooo:'+JSON.stringify(this.addEndtime))
          insertGateData(newGateData).then(() => {
            // 显示数据
            let data = {
              airportname: this.airportname,
            };
            selectGateData(data).then((res) => {
              // 保存机场数据
              let tmptable=JSON.parse(JSON.stringify(res.data));
              for(let i=0;i<tmptable.length;i++){
                tmptable[i].airportname=this.airportname;
              }
              this.subTableData=JSON.parse(JSON.stringify(tmptable));
              this.tableData = JSON.parse(JSON.stringify(tmptable));
              this.totalCount = this.tableData.length;
            }).then(() => {
                this.$message({
                  message: "插入数据成功！",
                  type: "success",
                  offset: 300,
                  center: true,
                  duration: 2000,
                });
              })
              .then(() => {
                //关闭对话框
                this.addGateDatadialogCancal();
              });
            });
            this.$refs['addTestDataFormRef'].resetFields();

        }
      }
    },
    // addTestDatadialogOpen() {
    //   this.airDate = "";
    //   this.addTestDatadialog = true;
    // },
    addGateDatadialogOpen() {
      this.addGateDatadialog = true;
