  watch: {
    selectTestData: function () {
      if (this.testDataNameList.length !== 0) {
        const index = this.testDataNameList.findIndex((item) => {
          if (item.id == this.selectTestData) {
            return true;
          }
        });
        let data = {
          filename: this.testDataNameList[index].filename,
          userId: this.username,
        };
        // console.log(data);
        selectTestData(data)
          .then((res) => {
            this.tableData = JSON.parse(JSON.stringify(res.data));
            this.subTableData = JSON.parse(JSON.stringify(this.tableData));
            this.totalCount = this.tableData.length;
          })
          .then(() => {
            //将监听到的测试数据名称保存在vuex中
            // console.log(this.testDataNameList[index].filename);
            this.$store.commit(
              "set_testDataNameVuex",
              this.testDataNameList[index].filename
            );
            //将监听到的机场名称保存在vuex中
            this.$store.commit(
              "set_airportNameVuex",
              this.tableData[0].airportname
            );
            // console.log(this.$store.state.airportNameVuex);
          });
      }
    },
    // 监听选择器中的机场名称-然后渲染日期选择器
    addAirportname: function () {
      this.airDate = "";
      this.airportData = [];
      const index = this.airportNameList.findIndex((item) => {
        if (item.id == this.addAirportname) {
          return true;
        }
      });
      let data = {
        airportname: this.airportNameList[index].airportname,
      };
      selectAirportData(data)
        .then((res) => {
          // 保存机场数据
          this.airportData = JSON.parse(JSON.stringify(res.data));
        })
        .then(() => {
          // 渲染日期选择器
          let dateList = [];
          //封装日期的列表-显示在选择器中
          for (let i = 0; i < this.airportData.length; i++) {
            let date = this.airportData[i].atime.substring(0, 11);
            // 转为日期
            dateList.push(new Date(date));
          }
          //minMaxDate保存了最小最大日期
          let minMaxDate = getMinMaxDate(dateList);
          let min = minMaxDate[0];
          this.airDate = min;
          this.pickerOptions = {
            disabledDate(time) {
              return (
                time.getTime() < minMaxDate[0] || time.getTime() > minMaxDate[1]
              );
            },
          };
        });
    },
  },
  created() {
    // console.log('hhhhh '+JSON.stringify(this.$store.state.airportNameVuex));
    this.airportname=this.$store.state.airportNameVuex;
    //登录后请求数据
    // 请求机场名称列表
    let data = {
        airportname: this.airportname,
      };
      selectGateData(data)
        .then((res) => {
          // 保存机场数据
          let tmptable=JSON.parse(JSON.stringify(res.data));
          for(let i=0;i<tmptable.length;i++){
            tmptable[i].airportname=this.airportname;
            this.gatestatelist[i]=tmptable[i].state;
          }
          this.subTableData=JSON.parse(JSON.stringify(tmptable));
          this.tableData = JSON.parse(JSON.stringify(tmptable));
          this.totalCount = this.tableData.length;
        }); 
  },
  
};
